import React from 'react';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import Divider from '@material-ui/core/Divider'
import Button from '@material-ui/core/Button'
import ChevronLeft from '@material-ui/icons/ChevronLeft'
import Header from '../components/Header.jsx'
import '../App.css';

import {large_threshold} from '../files/constants.js'

//Image imports
import donothing_img from '../images/donothing.png'

class AboutRfm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLarge: window.innerWidth > large_threshold
    }
  }

  componentWillMount() {
    window.addEventListener('resize', () => {
      this.setState({
        isLarge: window.innerWidth > large_threshold
      });
    }, false);
  }

  urlWithParams(base) {
    var params = "/";

    if(this.props.match.params.sah !== undefined){
      params = params + this.props.patient.sex + '/' + this.props.patient.age + '/' + this.props.patient.lesionDiameter + '/' + this.props.patient.lesionLocation + '/' + this.props.patient.ethnicity + '/' + this.props.patient.htn + '/' + this.props.patient.hxSAH + '/' + this.props.patient.studyPref
    }
    var url = '#/' + base + params;
    return url;
  }


  render() {
    var size_class = this.state.isLarge
      ? 'large'
      : '';
    return (<div>
              <Header patient={this.props.patient}/>
      <Grid container={true} item={true} justify={'flex-start'} alignContent={'flex-start'} className={'first-block main-content'} md={9} xs={12}>
        <Paper>
          <Grid item={true} container={true} xs={12}>
            <Typography variant='h3' className={'landing-title'}>About Lifestyle Modification:</Typography>
          </Grid>
          <Grid container={true} spacing={16} className={'norm_padding'} direction={'column'}>
            <Grid className={'content-block summary'} item={true} xs={12} container={true} direction={this.state.isLarge
                ? 'row-reverse'
                : 'column'} spacing={16}>
              <Grid item={true} xs={12} sm={6}>
                <img src={donothing_img} alt={'Unavailable at this time.'} className={size_class + ' limit_md'}/>
              </Grid>
              <Grid item={true} xs={12} sm={6} alignItems={'center'} container={true}>
                <div className={size_class + ' summary-block-text'}>
                  <Typography variant={'title'} gutterBottom={true}>Need to know: risk reduction</Typography>
                  <Typography variant={'body1'}>Other health conditions can contribute to aneurysms growing and becoming more likely to rupture. It is recommended that these conditions be treated to minimize the risk of rupture as much as possible. Your primary care provider can help you manage these conditions.</Typography>
                </div>
              </Grid>
            </Grid>
            <Divider/>
            <Grid className={'content-block'} item={true} xs={12} sm={6}>
              <Typography variant={'title'} gutterBottom={true}>How to lower risk:</Typography>
              <Typography variant={'body1'} paragraph={true}>Studies have suggest that certain health conditions may make aneurysms more likely to rupture. These include:
                <ul>
                  <li>High blood pressure</li>
                  <li>High cholesterol</li>
                  <li>Cigarette smoking</li>
                  <li>Excessive alcohol consumption</li>
                  <li>Hits to head - wear a helmet while riding bikes or motorcycles and a seatbelt while in cars.</li>
                </ul>
              </Typography>
              <Typography variant={'body1'} paragraph={true}>
                These health conditions are typically managed by your primary care provider with recommended changes to your daily activities and medications. Failing to treat these conditions may increase your risk of rupture. 
              </Typography>
            </Grid>
            <Divider/>
            <Grid item={true} xs={12} sm={6} className={'content-block'}>
              <Typography variant={'title'} gutterBottom={true}>Common misconceptions:</Typography>
              <Typography variant={'body1'} paragraph={true}>
                Although some studies have suggested that certain behaviors 'trigger' aneurysm rupture by raising blood pressure quickly, the following activities <b>do not need to be avoided by patients with low risk aneurysms</b>.

                <ul>
                  <li>
                    Sexual activity</li>
                  <li>Straining</li>
                  <li>Exercise</li>
                  <li>Nose blowing</li>
                </ul>
              </Typography>

            </Grid>
          <Grid item={true} container={true} xs={12} justify={'flex-end'}>
              <Button variant={'contained'} color={'primary'} href={this.urlWithParams('decision')}><ChevronLeft/>Go Back</Button>
            </Grid>
          </Grid>
        </Paper>
      </Grid>
    </div>)
  }
}

export default AboutRfm;
