const isuia5YRuptureRiskTable = {
  'xsmall': {
    'anterior': {
      'group1': 0,
      'group2': 1.5
    },
    'posterior': {
      'group1': 2.5,
      'group2': 3.4
    },
    'cca': {
      'group1': 0,
      'group2': 0
    }
  },
  'small': {
    'cca': 0,
    'anterior': 2.6,
    'posterior': 14.5
  },
  'medium': {
    'cca': 30,
    'anterior': 14.5,
    'posterior': 18.4
  },
  'large': {
    'cca': 6.4,
    'anterior': 40,
    'posterior': 50
  },
}


export function isuia5YRuptureRisk(location, size, hxSAH) {
  if (location === -1 || size === -1) {
    return null;
  }
  var locationBucket = 'anterior';
  if (location === 6 | location === 7 | location === 8 | location === 10 | location === 12) {
    locationBucket = 'posterior';
  } else if (location === 9) {
    locationBucket = 'cca';
  }

  var sizeBucket = 'xsmall';
  if (size > 7) {
    sizeBucket = 'small';
  } else if (size > 12) {
    sizeBucket = 'medium'
  } else if (size > 24) {
    sizeBucket = 'large'
  };

  if (sizeBucket === 'xsmall') {
    var group = hxSAH === true ? 'group2' : 'group1'
    return isuia5YRuptureRiskTable[sizeBucket][locationBucket][group]
  } else {
    return isuia5YRuptureRiskTable[sizeBucket][locationBucket]
  }
}


export function calculatePhases(patient) {
  var total = 0;
  var percent = 0;
  if (patient.ethnicity === -1 || patient.lesionDiameter === -1 || patient.lesionLocation === -1 || patient.age < 18) {
    return null;
  }
  if (patient.ethnicity === 1) {
    total = total + 5;
  } else if (patient.ethnicity === 2) {
    total = total + 3;
  };
  if (patient.htn === true) {
    total = total + 1;
  };
  if (patient.age >= 70) {
    total = total + 1;
  };

  if (patient.lesionDiameter > 20) {
    total = total + 10;
  } else if (patient.lesionDiameter > 10) {
    total = total + 6;
  } else if (patient.lesionDiameter > 7) {
    total = total + 3
  } else {
    //do nothing
  }

  if (patient.hxSAH === true) {
    total = total + 1
  }
  if (patient.lesionLocation <= 1) {
    //do nothing
  } else if (patient.lesionLocation === 5) {
    total = total + 2;
  } else {
    total = total + 4;
  }

  if (total >= 12) {
    percent = 17.8;
  } else if (total >= 11) {
    percent = 7.2;
  } else if (total >= 10) {
    percent = 5.3;
  } else if (total >= 9) {
    percent = 4.3;
  } else if (total >= 8) {
    percent = 3.2;
  } else if (total >= 7) {
    percent = 2.4;
  } else if (total >= 6) {
    percent = 1.7;
  } else if (total >= 5) {
    percent = 1.3;
  } else if (total >= 4) {
    percent = .9;
  } else if (total >= 3) {
    percent = .7;
  } else {
    percent = .4;
  }
  return percent;
}



