export function ucasYearlyRuptureRate(location, size) {



  if (location === -1 || size === -1) {

    return null;
  }
  var sizeBucket = '<3';
  if (size >= 2.5 && size < 4.5) {
    sizeBucket= '3-4'
  }
  if (size >= 4.5 && size < 6.5) {
    sizeBucket = '5-6'
  } else if (size >= 6.5 && size < 9.5) {
    sizeBucket = '7-9'
  } else if (size >= 9.5 && size < 24.5) {
    sizeBucket = '10-24'
  } else if (size >= 24.6) {
    sizeBucket = '>25'
  }
  if(sizeBucket === '<3') {
    return 0;

  }

  if(!Object.keys(ucasYearlyRuptureRateTable).includes(location)) {
    return ucasYearlyRuptureRateTable[12][sizeBucket]
  } else {
    return ucasYearlyRuptureRateTable[parseInt(location)][sizeBucket]
  }

}


