import React from 'react';
import {HashRouter as Router, Route, Redirect} from 'react-router-dom';
import DataEntry from './views/data-entry.jsx';
import Scores from './views/scores.jsx';
import Home from './views/home.jsx';
import Landing from './views/landing.jsx'
import Options from './views/options.jsx';
import Options2 from './views/options2.jsx';
import Options3 from './views/options3.jsx';
import Options4 from './views/options4.jsx';
import InitData from './views/initdata.jsx';
import ImgSlideshow from './views/img-slideshow.jsx';
import Decision from './views/decision.jsx';
import AboutSurg from './views/about-surg.jsx';
import AboutImg from './views/about-img.jsx';
import AboutRfm from './views/about-rfm.jsx';
import Values from './views/values.jsx';


export default class MainRouter extends React.Component {

  render() {
    return (<Router>
      <div>
        <Route path='/data-entry/:sex?/:age?/:size?/:location?/:ethnicity?/:htn?/:sah?/:studyPref?' render={(props) => <DataEntry{...props} location={props.location} updatePatient={(pat) => this.props.updatePatient(pat)} patient={this.props.patient}/>}/>
        <Route path='/scores/:sex?/:age?/:size?/:location?/:ethnicity?/:htn?/:sah?/:studyPref?' render={(props) => <Scores {...props}location={props.location} updatePatient={(pat) => this.props.updatePatient(pat)} patient={this.props.patient}/>}/>
        <Route path='/home/:sex?/:age?/:size?/:location?/:ethnicity?/:htn?/:sah?/:studyPref?' render={(props) => <Home {...props} location={props.location} updatePatient={(pat) => this.props.updatePatient(pat)} patient={this.props.patient}/>}/>
        <Route path='/decision/:sex?/:age?/:size?/:location?/:ethnicity?/:htn?/:sah?/:studyPref?' render={(props) => <Decision {...props} location={props.location} updatePatient={(pat) => this.props.updatePatient(pat)} patient={this.props.patient}/>}/>
        <Route path='/landing/:sex?/:age?/:size?/:location?/:ethnicity?/:htn?/:sah?/:studyPref?' render={(props) => <Landing {...props} location={props.location} updatePatient={(pat) => this.props.updatePatient(pat)} patient={this.props.patient}/>}/>
        <Route path='/initdata/:sex?/:age?/:size?/:location?/:ethnicity?/:htn?/:sah?/:studyPref?' render={(props) => <InitData {...props} location={props.location} updatePatient={(pat) => this.props.updatePatient(pat)} patient={this.props.patient}/>}/>
        <Route path='/options' render={(props) => <Options patient={this.props.patient}/>}/>
        <Route path='/options2' render={(props) => <Options2 patient={this.props.patient}/>}/>
        <Route path='/options4' render={(props) => <Options4 patient={this.props.patient}/>}/>
        <Route path='/about-rfm/:sex?/:age?/:size?/:location?/:ethnicity?/:htn?/:sah?/:studyPref?' render={(props) => <AboutRfm  {...props} location={props.location} patient={this.props.patient}/>}/>
        <Route path='/about-surg/:sex?/:age?/:size?/:location?/:ethnicity?/:htn?/:sah?/:studyPref?' render={(props) => <AboutSurg  {...props} location={props.location} patient={this.props.patient}/>}/>
        <Route path='/about-img/:sex?/:age?/:size?/:location?/:ethnicity?/:htn?/:sah?/:studyPref?' render={(props) => <AboutImg   {...props}location={props.location} patient={this.props.patient}/>}/>
        <Route path='/img-slideshow' render={(props) => <ImgSlideshow patient={this.props.patient}/>}/>
        <Route path='/values' render={(props) => <Values patient={this.props.patient}/>}/>
        <Route exact={true} path='/' render={() => <Redirect to={'/landing'}/>}/>
        <Route exact={true} path='' render={() => <Redirect to={'/landing'}/>}/>

      </div>
    </Router>)
  }
};
