import React from 'react';

import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper';
import Slider from 'react-rangeslider';
import Typography from '@material-ui/core/Typography'
import 'react-rangeslider/lib/index.css'


import {locationNumberToName} from '../files/constants.js'

import '../App.css';

class Values extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      sex: this.props.patient.sex,
      age: this.props.patient.age,
      lesionLocation: this.props.patient.lesionLocation,
      lesionDiameter: this.props.patient.lesionDiameter,
      htn: this.props.patient.htn,
      ethnicity: this.props.patient.ethnicity,
      hxSAH: this.props.patient.hxSAH,
      value1: 0,
      value2: 0
    }
  }

  handleTextChange = name => event => {
    this.setState({[name]: event.target.value});

  };
  handleToggleChange = name => event => {
    this.setState({[name]: event.target.checked});

  };

  locationOptions() {
    var div = Object.keys(locationNumberToName).map((key) => {
      return (<option key={key} value={key}>{locationNumberToName[key]}</option>)
    });
    return div;
  };

  urlWithParams(base) {
    var params = "/";
    params = params + this.state.sex + '/' + this.state.age + '/' + this.state.lesionDiameter + '/' + this.state.lesionLocation + '/' + this.state.ethnicity + '/' + this.state.htn + '/' + this.state.hxSAH;

    var url = '#/' + base + params;
    return url;
  }

  submit() {}

  handleSliderChange(value, q) {
    var valueName = 'value' + q
    console.log(valueName)
    this.setState({[valueName]: value})
  };

  render() {

    return (<div>
      <Grid container={true} justify={'flex-start'} alignContent={'flex-start'} spacing={8} className={'norm_padding main-content'}>

        <Grid item={true} xs={12}>
          <Paper className={'norm_padding'}>
            <div className={'value-slider'}>
              <Grid container={true} direction={'row'} justify={'space-between'}>
                <Typography variant='body2'>Having surgery is worth the risk to treat my aneurysm:</Typography>
                <Typography variant='body2'>It is imporant to avoid surgery if at all possible.</Typography>
              </Grid>
              <Slider min={-5} max={5} value={this.state.value1} onChange={(value) => this.handleSliderChange(value, 1)} withBars={true}/>
              <Grid container={true} direction={'row'} justify={'space-between'}>
                <Typography variant='body1'>Strongly Disagree</Typography>
                <Typography variant='body1'>Neither Agree nor Disagree</Typography>
                <Typography variant='body1'>Strongly Agree</Typography>

              </Grid>
            </div>
            <div className={'value-slider'}>
              <Grid container={true} direction={'row'} justify={'space-between'}>
                <Typography variant='body2'>I cannot live with the anxiety that I feel towards rupture.</Typography>
                    <Typography variant='body2'>I can live with the anxiety I feel towards rupture.</Typography>
                  </Grid>
                  <Slider min={-5} max={5} value={this.state.value2} onChange={(value) => this.handleSliderChange(value, 2)} withBars={true}/>
                  <Grid container={true} direction={'row'} justify={'space-between'}>
                    <Typography variant='body1'>Strongly Disagree</Typography>
                    <Typography variant='body1'>Neither Agree nor Disagree</Typography>
                    <Typography variant='body1'>Strongly Agree</Typography>

                  </Grid>
                </div>
              </Paper>
            </Grid>

          </Grid>
        </div>);
  }
}

export default Values;
