import React from 'react';

import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper';

import PatientDataForm from '../components/patientDataForm.jsx'
import Header from '../components/Header.jsx'
import '../App.css';

class DataEntry extends React.Component {
  constructor(props) {
    super(props);
  }

  urlWithParams(base) {
    var params = "/";
    params = params + this.props.patient.sex + '/' + this.props.patient.age + '/' + this.props.patient.lesionDiameter + '/' + this.props.patient.lesionLocation + '/' + this.props.patient.ethnicity + '/' + this.props.patient.htn + '/' + this.props.patient.sah + '/' + this.props.patient.studyPref;
    var url = '#/' + base + params;
    return url;
  }


  render() {
    return (<div>
        <Header patient={this.props.patient}/>
        <Grid container={true} item={true} justify={'flex-start'} alignContent={'flex-start'} className={'first-block main-content'} md={9} xs={12}>
          <Paper>
            <Grid container={true} justify={'flex-start'} alignContent={'flex-start'} className={'norm_padding'}>
              <PatientDataForm updatePatient={(pat) => this.props.updatePatient(pat)} patient={this.props.patient} destination={'scores'} skip={false}/>
            </Grid>
          </Paper>
        </Grid>
    </div>);
  }
}

export default DataEntry;
