import React from 'react'
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogContent from '@material-ui/core/DialogContent';



import '../App.css';

class UrlDialog extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      content: this.props.url
    }
  }

  render() {
    return (<div>
      <Dialog open={this.props.open} onClose={() => this.props.onClose()} aria-labelledby="simple-dialog-title">
        <DialogTitle className={'DialogTitle'}>Patient URL</DialogTitle>
        <DialogContent>
          <DialogContentText>
          {this.props.url}
          </DialogContentText>
        </DialogContent>
      </Dialog>
    </div>)

  }
}



  export default UrlDialog;
