import React from 'react'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'

import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Grid from '@material-ui/core/Grid'

export default class Header extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      menu: false,
      anchorEl: null,
      setAnchorel: null
    }
  }

  urlWithParams(base) {
    var params = "/";
    if (this.props.patient.hxsah !== undefined) {
      params = params + this.props.patient.sex + '/' + this.props.patient.age + '/' + this.props.patient.lesionDiameter + '/' + this.props.patient.lesionLocation + '/' + this.props.patient.ethnicity + '/' + this.props.patient.htn + '/' + this.props.patient.hxSAH + '/' + this.props.patient.studyPref
    }
    var url = '#/' + base + params;
    return url;
  }

  openMenu(event) {
    this.setState({
      menu: !this.state.menu,
      anchorEl: event.currentTarget
    })
  }

  closeMenu() {
    this.setState({
      menu: !this.state.menu,
      anchorEl: null
    })
  }

  render() {
    var loc = window.location.href;
    return (<AppBar position={'static'} color={'default'}>
      <Toolbar>
        <Grid container={true} direction-xs={'column'} direction-sm={'row'}>
          <Grid container={true} item={true} xs={12} sm={10} direction={'column'}>
            <Typography variant={'h6'} color={'primary'} className={'header-text'}>Aneurysm Patient Support Tool</Typography>
            <Grid container={true} item={true} className={'header-nav'} direction-xs={'column'} direction-sm={'row'}>
              <a href={this.urlWithParams('initdata')} className={loc.includes('initdata')
                  ? ''
                  : 'inactive'}>1. My Data</a>
              <a href={this.urlWithParams('home')} className={loc.includes('home')
                  ? ''
                  : 'inactive'}>2. About Aneurysms</a>
              <a href={this.urlWithParams('decision')} className={loc.includes('decision')
                  ? ''
                  : 'inactive'}>3. Treatment Options</a>
                <a href={this.urlWithParams('options4')} className={loc.includes('options4')
                  ? ''
                  : 'inactive'}>4. Compare Options</a>
            </Grid>
          </Grid>

          <Grid container={true} item={true} xs={12} sm={2} justify={'flex-end'}>

            <Button variant={'outlined'} color={'primary'} onClick={(e) => this.openMenu(e)} size={'small'}>
              Providers
            </Button>
            <Menu id="simple-menu" anchorEl={this.state.anchorEl} open={this.state.menu} onClose={() => this.closeMenu()}>
              <MenuItem>
                <a href={'#/data-entry'}>Risk Calculator</a>
              </MenuItem>
              <MenuItem>
                <a href={'#/img-slideshow'}>SlideShow</a>
              </MenuItem>
            </Menu>
          </Grid>
        </Grid>
      </Toolbar>
    </AppBar>)
  }
}
