import React, {Component} from 'react';

import MainRouter from './routes.jsx';

import {MuiThemeProvider, createMuiTheme} from '@material-ui/core/styles'

import Footer from './components/Footer.jsx'
import {isuia1YSurgicalOutcome, isuia1YEndoOutcome, isuia5YRuptureRisk, calculatePhases, ucasYearlyRuptureRate} from './files/constants.js'

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#E64C3C'
    },
    secondary: {
      main: '#90a4ae'
    }
  }
})

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      patient: {
        sex: -1,
        lesionLocation: -1,
        lesionDiameter: -1,
        age: -1,
        htn: false,
        hxSAH: false,
        ethnicity: -1,
        phases: null,
        isuia: null,
        isuiaEndo: null,
        isuiaSurg: null,
        ucas: null,
        studyPref: null,
      }
    }
    this.updatePatient = this.updatePatient.bind(this);
  }

  updatePatient(updatedPatient) {
    updatedPatient.phases = calculatePhases(updatedPatient);
    updatedPatient.isuia = isuia5YRuptureRisk(updatedPatient.lesionLocation, updatedPatient.lesionDiameter, updatedPatient.hxSAH);
    updatedPatient.isuiaEndo = isuia1YEndoOutcome(updatedPatient.age, updatedPatient.lesionLocation, updatedPatient.lesionDiameter);
    updatedPatient.isuiaSurg = isuia1YSurgicalOutcome(updatedPatient.age, updatedPatient.lesionLocation, updatedPatient.lesionDiameter);
    updatedPatient.ucas = ucasYearlyRuptureRate(updatedPatient.lesionLocation, updatedPatient.lesionDiameter)
    if(updatedPatient.ucas !== null) {
    updatedPatient.ucas = (updatedPatient.ucas / .2).toFixed(2);}

    if(updatedPatient.studyPref == null) {
      updatedPatient.studyPref=3
    }

    this.setState({patient: updatedPatient})
  }

  render() {
    return (<div className={'app_background'}>
      <MuiThemeProvider theme={theme}>
        <div className={'app_container'}>
          <MainRouter updatePatient={(pat) => this.updatePatient(pat)} patient={this.state.patient}/>
        </div>
      <Footer/>
      </MuiThemeProvider>
    </div>);
  }
}

export default App;
