import React from 'react';
import ReactDOM from 'react-dom'

import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'

import Paper from '@material-ui/core/Paper'

import Divider from '@material-ui/core/Divider';
import Header from '../components/Header.jsx'
import ProbGraph from '../components/ProbGraph.jsx'

import '../App.css';

import {large_threshold} from '../files/constants.js'
import {locationNumberToName, lifeExpectancy} from '../files/constants.js'

import {
  VictoryBar,
  VictoryChart,
  VictoryAxis,
  VictoryStack,
  VictoryTheme,
  VictoryLabel
} from 'victory';

import pron_file from '../files/ca_pronounced.mp3'

import ChevronRight from '@material-ui/icons/ChevronRight'

//
var pron = new Audio(pron_file)

class Scores extends React.Component {

  updatePatientWithParams() {
    if (typeof this.props.match.params.age !== 'undefined') {
      var newPatient = this.props.patient;
      newPatient.sex = this.props.match.params.sex;
      newPatient.age = this.props.match.params.age;
      newPatient.lesionDiameter = this.props.match.params.size;
      newPatient.lesionLocation = this.props.match.params.location;
      newPatient.ethnicity = this.props.match.params.ethnicity;
      newPatient.htn = this.props.match.params.htn === 'true'
      newPatient.hxSAH = this.props.match.params.sah === 'true'
      this.props.updatePatient(newPatient);
    }
  }
  constructor(props) {

    super(props);
      var graphProbLoc = 'isuia'
    switch (this.props.match.params.studyPref) {
      case '1':
        graphProbLoc = 'ucas'
        break;
      case '2':
        graphProbLoc = 'isuia'
        break;
      case '3':
        graphProbLoc = 'phases'
        break;
      case '4':
        graphProbLoc = 'isuia'
        break;
      default:

        break;
    }

    this.state = {
      isLarge: window.innerWidth > large_threshold,
      location: this.props.match.params.location !== 'undefined'
        ? locationNumberToName[this.props.match.params.location]
        : '',

      graphProb: graphProbLoc
    }

    this.updatePatientWithParams();

  }
  componentWillMount() {
    window.scrollTo(0, 0);
    window.addEventListener('resize', () => {
      this.setState({
        isLarge: window.innerWidth > large_threshold
      });
    }, false);

  }

  componentDidUpdate() {
    var hash = this.props.location.hash.replace('#', '');
    if (hash) {
      let node = ReactDOM.findDOMNode(this.refs[hash]);
      if (node) {
        node.scrollIntoView()
        window.scrollBy(0, -100);
      }
    }
  }

  pronounce() {
    pron.play();
  }

  locationStatement() {
    var div = null;
    if (typeof this.state.location !== 'undefined') {
      div = " Your aneurysm is located on the " + locationNumberToName[this.props.match.params.location] + ".";
    }
    return div;
  }





  setGraphProb(study) {
    this.setState({graphProb: study});
  }

  lifetimeRisk(annualRuptureRisk) {
    var remaining = lifeExpectancy[this.props.patient.sex][this.props.patient.age];
    return  Math.min((((remaining) * (1 - (1 - annualRuptureRisk / 100))) * 100).toFixed(2),100);
  }

  compareChart() {
    var valMon = parseInt(this.lifetimeRisk(this.props.patient[this.state.graphProb] / 5));
    if (valMon > 100) {
      valMon = 100
    }
    var valClip = parseInt(this.props.patient.isuiaSurg);;
    var valCoil = parseInt(this.props.patient.isuiaEndo);

    var dataBad = [
      {
        Choice: 'Monitor',
        Risk: valMon
      }, {
        Choice: 'Coiling',
        Risk: valCoil
      }, {
        Choice: 'Clipping',
        Risk: valClip
      }
    ];
    var dataGood = [
      {
        Choice: 'Monitor',
        Risk: 100 - valMon
      }, {
        Choice: 'Coiling',
        Risk: 100 - valCoil
      }, {
        Choice: 'Clipping',
        Risk: 100 - valClip
      }
    ]

    return (<VictoryChart domainPadding={20} theme={VictoryTheme.material}>
      <VictoryAxis tickValues={[1, 2, 3]} tickFormat={["Monitor", "Coiling", "Clipping"]}/>
      <VictoryAxis dependentAxis={true} axisLabelComponent={<VictoryLabel dy = {-30} />} label={'Risk (%)'} tickFormat={(x) => `${x}%`}/>
      <VictoryStack>
        <VictoryBar data={dataBad} x={'Choice'} y={'Risk'} style={{
            data: {
              fill: "#be202e"
            }
          }}/>
        <VictoryBar data={dataGood} x={'Choice'} y={'Risk'} style={{
            data: {
              fill: "#EEEEEE"
            }
          }}/>
      </VictoryStack>
    </VictoryChart >);
  }

  render() {
    var size_class = this.state.isLarge
      ? 'large'
      : '';
    return (<div>
              <Header patient={this.props.patient}/>


        <Grid container={true} item={true} justify={'flex-start'} alignContent={'flex-start'} className={'first-block main-content'} md={9} xs={12}>

          <Paper>
            <Grid container={true} direction={'column'} alignContent={'stretch'}>
              <Grid item={true} container={true} xs={12}>
                <Typography variant='h3' className={'landing-title'}>Patient information:</Typography>
              </Grid>

              <Grid id={'risk'} item={true} container={true} xs={12} className={'content-block '} alignItems={'center'}>
                {
                  this.props.match.params.sah != null
                    ? <>
                    <Grid item = {true}
                    container = {
                      true
                    }
                    direction = {
                      'column'
                    }
                    xs = {
                      12
                    }
                    spacing = {
                      16
                    } > </Grid>
                    <Grid item={true} xs={12} container={true} direction={size_class === 'large'
                        ? 'row'
                        : 'column'}>
                      <Grid container={true} item={true} xs={12} sm={6} md={8} direction={'column'} alignItems={'flex-start'} justify={'center'}>
                        <Grid item={true} container={true}>
                          <Typography variant={'h5'}>My Risk of Rupture in Next 5 Years:</Typography>
                          <Typography variant={'body1'} paragraph={true}>These data are from the major studies on unruptured aneurysms. Click on a number to see it in the graph on the right.</Typography>
                          {
                            this.props.patient.age <= 75
                              ? <Typography variant={'body1'} paragraph={true}>The small gray numbers show the calculated chance that a person with this level of risk would have a rupture in their whole life based on average life expectancy in the USA.</Typography>
                              : null
                          }
                          <Grid item={true} container={true} spacing={16} direction={'row'} xs={12} justify={'center'}>
                            {
                              this.props.patient.ucas !== null & (this.props.match.params.studyPref === 1 | this.props.match.params.studyPref === 4)
                                ? <Grid item={true} container={true} direction={'column'} alignItems={'center'} xs={4} onClick={() => this.setGraphProb('ucas')}>
                                    <p className={'supertitle'}>UCAS</p>
                                    <Typography variant={'h3'} justify={'center'} color={'primary'} className={'big-number'}>{this.props.patient.ucas}%</Typography>
                                    {
                                      this.props.patient.age <= 75
                                        ? <Typography variant={'body1'} justify={'center'} color={'secondary'} className={'lifetime-mortality'}>{this.lifetimeRisk(this.props.patient.ucas / 5)}%</Typography>
                                        : null
                                    }
                                  </Grid>
                                : null
                            }
                            {
                              (this.props.match.params.studyPref === 2 | this.props.match.params.studyPref === 4)
                                ? <Grid item={true} container={true} direction={'column'} alignItems={'center'} xs={4} onClick={() => this.setGraphProb('isuia')}>
                                    <p className={'supertitle'}>ISUIA</p>
                                    <Typography variant={'h3'} justify={'center'} color={'primary'} className={'big-number'}>{this.props.patient.isuia}%</Typography>
                                    {
                                      this.props.patient.age <= 75
                                        ? <Typography variant={'body1'} justify={'center'} color={'secondary'} className={'lifetime-mortality'}>{this.lifetimeRisk(this.props.patient.isuia / 5)}%</Typography>
                                        : null
                                    }
                                  </Grid>
                                : null
                            }
                            {
                              (this.props.match.params.studyPref === 3 | this.props.match.params.studyPref === 4)
                                ? <Grid item={true} container={true} direction={'column'} alignItems={'center'} xs={4} onClick={() => this.setGraphProb('phases')}>
                                    <p className={'supertitle'}>PHASES</p>
                                    <Typography variant={'h3'} justify={'center'} color={'primary'} className={'big-number'}>{this.props.patient.phases}%</Typography>
                                    {
                                      this.props.patient.age <= 75
                                        ? <Typography variant={'body1'} justify={'center'} color={'secondary'} className={'lifetime-mortality'}>{this.lifetimeRisk(this.props.patient.phases / 5)}%</Typography>
                                        : null
                                    }
                                  </Grid>
                                : null
                            }
                          </Grid>

                        </Grid>
                      </Grid>
                      <Grid item={true} xs={12} sm={6} md={4} container={true} direction={'column'} alignItems={'center'} justify={'center'}>
                        <Typography variant={'body1'}>
                          <b>Graph of Risk from <span className={'all-caps'}>{this.state.graphProb}</span> Study</b>
                        </Typography>
                        <ProbGraph prob={this.props.patient[this.state.graphProb]}/>
                      </Grid>
                    </Grid>
                  </>
                    : <div>
                        <Typography variant={'h6'} gutterBottom={true}>What is my risk of rupture?</Typography>
                        <Typography variant={'body1'} gutterBottom={true} paragraph={true}>There are 3 large studies of aneurysms that are used to predict how likely an aneurysm is to rupture. If you know details about your aneurysm, you can use the calculator below to see an estimate of your individual risk. Please note, that the risk may be more or less as accurate for certain patients depending on how many patients like you were in the study. our actual risk of rupture may be higher or lower based on other parts of your health history or characteristics of your aneurysm.</Typography>
                        <Button fullWidth={true} variant={'outlined'} color={'primary'} href="#/initdata">Risk Calculator</Button>
                      </div>
                }

              </Grid>

              <Divider/>

            <Grid id={'decisionframe'} item={true} container={true} spacing={16} xs={12} className={'content-block'} alignItems={'center'}>

              {
                this.props.match.params.sah != null
                  ? <Grid item={true} xs={12}  container={true} justify={'center'} alignItems={'center'} direction={'column'}>
                      <Typography variant={'body1'}>
                        Percent Risk in Different Management Options
                      </Typography>
                      <Typography >
                        <b style={{
                            color: '#777777'
                          }}>Good outcome
                        </b>
                        <b style={{
                            color: '#be202e'
                          }}>
                          Bad outcome</b>
                      </Typography>

                      {this.compareChart()}


                    </Grid>

                  : null
              }

              <Grid item={true} container={true} xs={12} justify={'flex-end'}>
                <Button href='#/options' color={'primary'} size={'large'} variant={'contained'}>See Comparison<ChevronRight/></Button>
              </Grid>
            </Grid>
          </Grid>
        </Paper>


    </Grid>
  </div >)
  }
}
export default Scores;
