import React from 'react';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import Collapse from '@material-ui/core/Collapse';
import Paper from '@material-ui/core/Paper';
import Divider from '@material-ui/core/Divider';

import ChevronLeft from '@material-ui/icons/ChevronLeft'
import Header from '../components/Header.jsx'
import {large_threshold} from '../files/constants.js'

//Image imports

import lab_coiling_img from '../images/labeled_coiling.svg'
import lab_clipping_img from '../images/labeled_clipping.svg'
import clip_v_coil_img from '../images/clip_v_coil.svg'

import ExpandMoreIcon from '@material-ui/icons/ExpandMore'

class AboutSurg extends React.Component {
  constructor(props) {

    super(props);
    this.state = {
      isLarge: window.innerWidth > large_threshold,
      coilExp: false,
      clipExp: false,
    }
  }

  componentWillMount() {
    window.addEventListener('resize', () => {
      this.setState({
        isLarge: window.innerWidth > large_threshold
      });
    }, false);
  }
  urlWithParams(base) {
    var params = "/";

    if(this.props.match.params.sah !== undefined){
      params = params + this.props.patient.sex + '/' + this.props.patient.age + '/' + this.props.patient.lesionDiameter + '/' + this.props.patient.lesionLocation + '/' + this.props.patient.ethnicity + '/' + this.props.patient.htn + '/' + this.props.patient.hxSAH + '/' + this.props.patient.studyPref
    }
    var url = '#/' + base + params;
    return url;
  }
  toggleCoilExp() {
    this.setState({
      coilExp: !this.state.coilExp
    });
  }
  toggleClipExp() {
    this.setState({
      clipExp: !this.state.clipExp
    });
  }



  render() {
    var size_class = this.state.isLarge
      ? 'large'
      : ''
    return (<div>
              <Header patient={this.props.patient}/>
      <Grid container={true} item={true} justify={'flex-start'} alignContent={'flex-start'} className={'first-block main-content'} md={9} xs={12}>
        <Paper>
          <Grid item={true} container={true} xs={12}>
            <Typography variant='h3' className={'landing-title'}>About Surgery:</Typography>
          </Grid>
          <Grid container={true} spacing={16} className={'norm_padding'} direction={'column'}>
            <Grid className={'content-block summary'} item={true} xs={12} container={true} direction={this.state.isLarge
                ? 'row-reverse'
                : 'column'} spacing={16}>

              <Grid item={true} xs={12} sm={6}>
                <img src={clip_v_coil_img} alt={'Unavailable at this time.'} className={size_class}/>
              </Grid>
              <Grid item={true} xs={12} sm={6} alignItems={'center'} container={true}>
                <div className={size_class + ' summary-block-text'}>
                  <p className={'supertitle'}>Treatment:</p>
                  <Typography variant='title' gutterBottom={true}>Need to know: aneurysm surgery</Typography>
                  <Typography variant='body1' paragraph={true}>If an anuerysm is to be treated with surgery, there are 2 approaches to doing so.
                    <ul>
                      <li>Endovascular surgery. Also called <b>'coiling.'</b>
                      </li>
                      <li>Open surgery. Also called <b>'clipping.'</b>
                      </li>
                    </ul>
                    Choosing between endovascular surgery and open surgery can be complicated depending on your medical history and aneurysm, although sometimes one will clearly be a better option. Your doctor will help you understand which is best for you.
                  </Typography>
                </div>
              </Grid>
            </Grid>
            <Divider/>
            <Grid item={true} xs={12} className={'content-block'}>
              <Typography variant={'title'} gutterBottom={'true'}>There are 2 surgical approaches:</Typography>
              <Typography variant={'body1'} paragraph={true}>Your provider will determine which option is better for you.</Typography>
              <Grid item={true} xs={12} container={true} spacing={16}>
                <Grid item={true} xs={12} sm={6}>
                  <Paper className={'norm_padding'}>
                    <Typography variant={'title'} gutterBottom={'true'}>Coiling</Typography>
                    <Typography variant={'body2'} gutterBottom={'true'}>A surgery done from within the blood vessels. A thin, flexible tube passes through the arteries in the leg up to the brain to treat the aneurysm from inside the vessel.</Typography>
                    <img src={lab_coiling_img} alt={'Unavailable at this time.'}/>
                    <Grid alignItems={'center'} container={true} justify={'space-between'}>
                      <Typography variant={'body1'}>
                        <b>Procedure:</b> takes several hours.
                      </Typography>
                      <Button onClick={() => this.toggleCoilExp()}><ExpandMoreIcon/></Button>
                    </Grid>

                    <Collapse in={this.state.coilExp} timeout="auto" unmountOnExit={true}>
                      <Typography>
                        <ol>
                          <li>Arrive to operating room where sedating medications are given.</li>
                          <li>Catheter is inserted in an artery in the leg or arm. A catheter is a small, flexible tube.</li>
                          <li>Catheter travels through the arteries in the body to the aneurysm.</li>
                          <li>Metal coils are placed in the aneurysm through the catheter. The aneurysm is packed fully with coils. Depending on the aneurysm, a tube-shaped piece of mesh may also be placed inside the artery.</li>
                          <li>The catheter is removed, leaving the coils behind.</li>
                        </ol>
                      </Typography>
                    </Collapse>
                    <Typography variant={'body1'}>
                      <b>Recovery:</b>
                      <ul>
                        <li>Stay overnight in hospital intensive care unit with leg immobilized if a the artery in the leg was used. You will be awake.</li>
                        <li>A small scar will may form on the arm or leg where the catheter was placed.</li>
                        <li>Brain imaging and then clinic visit several weeks after to ensure that the aneurysm has been cured.</li>
                      </ul>
                    </Typography>
                    <Typography variant={'body1'}>
                      <b>Risks:</b>
                      <ul>
                        <li>Stroke causing temporary or permanent neurological damage.</li>
                        <li>Rupture of the aneurysm during surgery.</li>
                        <li>Hematoma (a bruise) at the site where the artery is accessed that may require a blood transfusion.</li>
                      </ul>
                            </Typography>
                  </Paper>
                </Grid>
                <Grid item={true} xs={12} sm={6}>
                  <Paper className={'norm_padding'}>
                    <Typography variant={'title'} gutterBottom={'true'}>Clipping</Typography>
                    <Typography variant={'body2'} gutterBottom={'true'}>A surgery done through an opening  in the skull. The surgeon finds the aneurysm and places a metal clip around its base, treating it from outside the vessel.</Typography>
                    <img src={lab_clipping_img} alt={'Unavailable at this time.'}/>

                    <Grid alignItems={'center'} container={true} justify={'space-between'}>
                      <Typography variant={'body1'}>
                        <b>Procedure:</b> takes several hours.
                      </Typography>
                      <Button onClick={() => this.toggleClipExp()}><ExpandMoreIcon className={this.state.clipExp
        ? 'rotate-180 now'
        : 'rotate-180'}/></Button>
                    </Grid>

                    <Collapse in={this.state.clipExp} timeout="auto" unmountOnExit={true}>
                      <Typography variant={'body1'}>
                        <ol>
                          <li>Arrive to operating room where you will put to sleep under general anesthesia</li>
                          <li>An opening is made by removing some of the bone in the skull.</li>
                          <li>The brain is spread so that the surgeon can see the aneurysm.</li>
                          <li>A metal clip is placed around the aneurysm, pinching it closed from the artery.</li>
                          <li>All tools are removed, leaving the clip behind.</li>
                          <li>The bone that was removed earlier is put back and fixed in place with screws, closing the opening in the skull.
                          </li>
                        </ol>
                      </Typography>
                    </Collapse>

                    <Typography variant={'body1'}>

                      <b>Recovery:</b>
                      <ul>
                        <li>Stay for a few days in the hospital beginning in the intensive care unit and then move to a normal hospital room when you are stable.</li>
                        <li>A scar may form at where the skin was cut to make an opening in the skull. Hair may have been cut to decrease the risk of infection.</li>
                        <li>Brain imaging and then clinic visit several weeks after to ensure that the aneurysm has been cured.</li>
                      </ul>
                    </Typography>
                    <Typography variant={'body1'}>

                      <b>Risks:</b>
                      <ul>
                        <li>Stroke causing temporary or permanent neurological damage.</li>
                        <li>Rupture of the aneurysm during surgery.</li>
                        <li>Infection of the brain or skin where the incision was made.</li>
                      </ul>
                    </Typography>
                  </Paper>
                </Grid>
              </Grid>
            </Grid>
            <Divider/>
            <Grid className={'content-block'} item={true} xs={12}>
              <Typography variant={"title"} gutterBottom={true}>Life after Surgery:</Typography>
              <Typography variant={'body1'} paragraph={true}>
                Even if a surgery goes well, it can take some time to recover, and each person is different. Many people who have had aneurysms treated recommend that you take time to ensure that you are feeling well before returning to full-time work.
              </Typography>
            </Grid>


            <Grid item={true} container={true} xs={12} justify={'flex-end'}>
                <Button variant={'contained'} color={'primary'} href={this.urlWithParams('decision')}><ChevronLeft/>Go Back</Button>
              </Grid>
          </Grid>
        </Paper>
      </Grid>
      </div>)
  }
};;

export default AboutSurg;
