import React from 'react';

import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'


import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import ChevronRight from '@material-ui/icons/ChevronRight'
import Header from '../components/Header.jsx'
import '../App.css';

class Options extends React.Component {
  updatePatientWithParams() {
    if (typeof this.props.match.params.age !== 'undefined') {
      var newPatient = this.props.patient;
      newPatient.sex = this.props.match.params.sex;
      newPatient.age = this.props.match.params.age;
      newPatient.lesionDiameter = this.props.match.params.size;
      newPatient.lesionLocation = this.props.match.params.location;
      newPatient.ethnicity = this.props.match.params.ethnicity;
      newPatient.htn = this.props.match.params.htn === 'true'
      newPatient.hxSAH = this.props.match.params.sah === 'true'
      newPatient.studyPref= this.props.match.params.studyPref
      this.props.updatePatient(newPatient);
    }
  }
  constructor(props) {
    super(props);
    this.state = {}
  }

  componentWillMount() {
      window.scrollTo(0,0);
    }

  render() {
    return (<div>
              <Header patient={this.props.patient}/>
            <Grid container={true} spacing={16} className={'norm_padding'}>
      <Paper>
      <Grid container={true} justify={'flex-start'} alignContent={'flex-start'} spacing={8} className={'norm_padding main-content'}>

        <Grid item={true} xs={12} className={'content-block'}>
          <p className={'supertitle'}>Treatment:</p>

          <Typography variant={'title'}>Compare Your Options</Typography>
        <Table className={'options-table'}>
          <TableHead>
            <TableRow hover={true}>
              <TableCell></TableCell>
              <TableCell >Risk Reduction Only</TableCell>
              <TableCell >Watchful Waiting</TableCell>
              <TableCell >Clipping or Coiling</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            <TableRow hover={true}>
              <TableCell component="th" scope="row">
                Goal
              </TableCell>
              <TableCell>Return to normal life.</TableCell>
              <TableCell>Get more information through time to determine risk of rupture.</TableCell>
              <TableCell>Eliminate risk of rupture.</TableCell>
            </TableRow>
            <TableRow hover={true}>
              <TableCell component="th" scope="row">
                Medical Benefits
              </TableCell>
              <TableCell>No risk of complications from testing or surgery.</TableCell>
              <TableCell>Avoid risk of surgery while collecting more information to determine risk of rupture.</TableCell>
              <TableCell>Risk of rupture eliminated.</TableCell>
            </TableRow>
            <TableRow hover={true}>
              <TableCell component="th" scope="row">
                Medical Risks
              </TableCell>
              <TableCell>Aneurysm not cured.</TableCell>
              <TableCell>Aneurysm not cured. Some risk of complications with testing.</TableCell>
              <TableCell>Surgery may cause bleeding, a stroke, or infection that can cause permanent neurological disability. Surgery may be unsuccessful, requiring another surgery.</TableCell>
            </TableRow >
            <TableRow hover={true}>
              <TableCell component="th" scope="row">
                Lifestyle Benefits
              </TableCell>
              <TableCell>No need for further followup or testing.</TableCell>
              <TableCell>Testing can be scheduled flexibly and does not require staying at the hospital. Provides peace of mind to some patients.</TableCell>
              <TableCell>Provides peace of mind to some patients after surgery.</TableCell>
            </TableRow>
            <TableRow hover={true}>
              <TableCell component="th" scope="row">
                Lifestyle Risks
              </TableCell>
              <TableCell>Some patients continue to be anxious about rupture.</TableCell>
              <TableCell>Repeated testing can be inconvenient.</TableCell>
              <TableCell>Surgery requires an overnight stay at the hospital for most patients as well as followup testing.</TableCell>
            </TableRow>
          </TableBody>
        </Table>
        </Grid>
        <Grid item={true} container={true} justify={'flex-end'} xs={12}>
          <Button href={'#/options2'}>OptionsV2 <ChevronRight/></Button>

          <Button href={'#/options4'}>OptionsV4 <ChevronRight/></Button>
        </Grid>
      </Grid>
      </Paper>
    </Grid>
    </div>)
  }
}

export default Options;
