import React from 'react';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'

import TextField from '@material-ui/core/TextField';
import Checkbox from '@material-ui/core/Checkbox';
import NativeSelect from '@material-ui/core/NativeSelect';
import Input from '@material-ui/core/Input';
import Button from '@material-ui/core/Button';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import InputAdornment from '@material-ui/core/InputAdornment';
import Divider from '@material-ui/core/Divider'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup';
import FormLabel from '@material-ui/core/FormLabel';

import UrlDialog from './urldialog.jsx'

import '../App.css';

import {locationNumberToName} from '../files/constants.js'

class PatientDataForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sex: this.props.patient.sex,
      age: this.props.patient.age,
      lesionLocation: this.props.patient.lesionLocation,
      lesionDiameter: this.props.patient.lesionDiameter,
      htn: this.props.patient.htn,
      ethnicity: this.props.patient.ethnicity,
      hxSAH: this.props.patient.hxSAH,
      studyPref: this.props.patient.studyPref,
      urlDialogOpen: false,
      patientURL: ""
    }
  }

  handleTextChange = name => event => {
    this.setState({[name]: event.target.value});
  };
handleTextChange
  handleToggleChange = name => event => {
    this.setState({[name]: event.target.checked});
  };

  handleStudyChange = event => {
    this.setState({studyPref:event.target.value})
  }

  locationOptions() {
    var div = Object.keys(locationNumberToName).map((key) => {
      return (<option key={key} value={key}>{locationNumberToName[key]}</option>)
    });
    return div;
  };

  urlWithParams(base) {
    var params = "/";
    if(this.state.studyPref == null) {
      this.setState({'studyPref':3})
    }
     params = params + this.state.sex + '/' + this.state.age + '/' + this.state.lesionDiameter + '/' + this.state.lesionLocation + '/' + this.state.ethnicity + '/' + this.state.htn + '/' + this.state.hxSAH + '/' + this.state.studyPref;
    var url = '#/' + base + params;
    return url;
  }

  handleURLDialogClose = () => {
    this.setState({urlDialogOpen: false})
  }
  handleURLDialogOpen = () => {
    var url = 'jrfeler.github.io/aneurysm_sdm/' + this.urlWithParams('landing')

    this.setState({
      patientURL:url,
      urlDialogOpen: true})
  }

  renderURLDialog() {
    return(<UrlDialog url={'jrfeler.github.io/aneurysm_sdm/' + this.urlWithParams('landing')} open={this.state.urlDialogOpen} onClose={this.handleURLDialogClose}/>)

  }


  submit() {
    var newPatient = this.props.patient;
    newPatient.age = this.state.age;
    newPatient.lesionDiameter = this.state.lesionDiameter;
    newPatient.lesionLocation = this.state.lesionLocation;
    newPatient.htn = this.state.htn;
    newPatient.ethnicity = this.state.ethnicity;
    newPatient.hxSAH = this.state.hxSAH;
    newPatient.studyPref = this.state.studyPref;
    console.log(newPatient)
    this.props.updatePatient(newPatient);
  }

  render() {
    return (<div>
      <Grid container={true} spacing={24} justify={'flex-end'}>
        <Grid item={true} xs={12}>
          <Typography component="h2" variant="h4" gutterBottom={true}>
            Patient Information
          </Typography>
          <Typography variant="subtitle1" gutterBottom={true}>Enter the information below to personalize the information that follows.</Typography>
          <Divider/>
        </Grid>
        <Grid item={true} xs={12} sm={2}>
          <TextField required={true} id="age" name="age" label="Age" fullWidth={true} defaultValue={this.state.age !==-1
              ? this.state.age
              : ""} InputProps={{
              endAdornment: <InputAdornment position="end">years</InputAdornment>
            }} onChange={this.handleTextChange('age')}/>

        </Grid>
        <Grid item={true} xs={12} sm={4}>
          <FormControl fullWidth={true}>
            <InputLabel htmlFor="location-native-simple">Sex assigned at birth</InputLabel>
            <NativeSelect value={this.state.sex !==-1
                ? this.state.sex
                : ""} input={<Input name = "sex" id = "sex" onChange = {
                this.handleTextChange('sex')
              } />}>
              <option value={-1}/>
              <option value={0}>Male</option>
              <option value={1}>Female</option>
            </NativeSelect>
          </FormControl>
        </Grid>
        <Grid item={true} xs={12} sm={6}>
          <FormControl fullWidth={true}>
            <InputLabel htmlFor="ethnicity-native-simple">Ethnicity</InputLabel>
            <NativeSelect value={this.state.ethnicity !==-1
                ? this.state.ethnicity
                : ""} input={<Input name = "ethnicity" id = "ethnicity" onChange = {
                this.handleTextChange('ethnicity')
              } />}>
              <option value={-1}/>
              <option value={1}>Finnish</option>
              <option value={2}>Japanese</option>
              <option value={0}>Neither Finnish nor Japanese</option>
            </NativeSelect>
          </FormControl>
        </Grid>
        <Grid item={true} xs={12} sm={6}>
          <TextField fullWidth={true} required={true} id="lesionDiameter" name="lesionDiameter" label="Aneurysm  Size or Diameter" defaultValue={this.state.lesionDiameter !==-1
              ? this.state.lesionDiameter
              : ""} onChange={this.handleTextChange('lesionDiameter')} InputProps={{
              endAdornment: <InputAdornment position="end">mm</InputAdornment>
            }}/>
        </Grid>

        <Grid item={true} xs={12} sm={6}>
          <FormControl >
            <InputLabel htmlFor="location-native-simple">Aneurysm Location</InputLabel>
            <NativeSelect fullWidth={true} value={this.state.lesionLocation !==-1
                ? this.state.lesionLocation
                : ""} input={<Input name = "lesionLocation" id = "lesionLocation" onChange = {
                this.handleTextChange('lesionLocation')
              } />}>
              <option value={-1}/> {this.locationOptions()}
            </NativeSelect>
          </FormControl>
        </Grid>

        <Grid item={true} xs={12} sm={6}>
          <FormControlLabel control={<Checkbox checked = {
              this.state.htn
            }
            onChange = {
              this.handleToggleChange('htn')
            }
            value = {
              'htn'
            } />} label="I have high blood pressure."/>
        </Grid>
        <Grid item={true} xs={12} sm={6}>
          <FormControlLabel control={<Checkbox checked = {
              this.state.hxSAH
            }
            onChange = {
              this.handleToggleChange('hxSAH')
            }
            value = {
              'hxSAH'
            } />} label="I have had a brain bleed before."/>
        </Grid>

        {window.location.href.includes('data-entry')?
        <Grid item={true} xs={12}>

          <FormLabel component="legend">Study data to show</FormLabel>
          <RadioGroup
            aria-label="study"
            name="study"
            value={this.state.studyPref}
            onChange={this.handleStudyChange}
          >
            <FormControlLabel value="1" control={<Radio />} label="UCAS" />
            <FormControlLabel value="2" control={<Radio />} label="ISUIA" />
            <FormControlLabel value="3" control={<Radio />} label="PHASES" />
              <FormControlLabel value="4" control={<Radio />} label="All" />

          </RadioGroup>

        </Grid> : null}


          {
            this.props.skip
              ? <Grid item={true} xs={12} sm={6}>
                  <Button fullWidth={true} variant={'outlined'} color={'primary'} href={'#/' + this.props.destination}>I do not know.</Button>
                </Grid>
              :
              <Grid item={true} xs={12} sm={6}>
                  <Button fullWidth={true} variant={'outlined'} color={'primary'} onClick={() => this.handleURLDialogOpen()} disabled={this.state.sex === -1 || this.state.age <= 17 || this.state.lesionDiameter === -1 || this.state.lesionLocation === -1 || this.state.ethnicity === -1}>Get Patient URL</Button>
                    {
                      this.state.sex === -1 || this.state.age <= 17 || this.state.lesionDiameter === -1 || this.state.lesionLocation === -1 || this.state.ethnicity === -1
                        ? <Typography variant={'caption'} color={'secondary'}>Please complete the form before clicking.</Typography>
                        : null
                    }
              </Grid>

          }
          <Grid item={true} xs={12} sm={6}>
            <Button fullWidth={true} variant={'contained'} color={'primary'} onClick={() => this.submit()} href={this.urlWithParams(this.props.destination)} disabled={this.state.sex === -1 || this.state.age <= 17 || this.state.lesionDiameter === -1 || this.state.lesionLocation === -1 || this.state.ethnicity === -1}>Next</Button>

          </Grid>

      </Grid>
      {this.renderURLDialog()}
    </div>);
  }
}

export default PatientDataForm;
