import React from 'react';
import ReactDOM from 'react-dom'

import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'

import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';

import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import Paper from '@material-ui/core/Paper'

import Divider from '@material-ui/core/Divider';

import ProbGraph from '../components/ProbGraph.jsx'
import Header from '../components/Header.jsx'
import '../App.css';

import {large_threshold} from '../files/constants.js'
import {locationNumberToName, lifeExpectancy} from '../files/constants.js'

import {
  VictoryBar,
  VictoryChart,
  VictoryAxis,
  VictoryStack,
  VictoryTheme,
  VictoryLabel
} from 'victory';

//Image imports

import coiling_img from '../images/coiling.svg'
import donothing_img from '../images/donothing.png'
import angio_img from '../images/angio.jpg'

import pron_file from '../files/ca_pronounced.mp3'

import ChevronRight from '@material-ui/icons/ChevronRight'
import ChevronLeft from '@material-ui/icons/ChevronLeft'

//
var pron = new Audio(pron_file)

class Decision extends React.Component {

  updatePatientWithParams() {
    if (typeof this.props.match.params.age !== 'undefined') {
      var newPatient = this.props.patient;
      newPatient.sex = this.props.match.params.sex;
      newPatient.age = this.props.match.params.age;
      newPatient.lesionDiameter = this.props.match.params.size;
      newPatient.lesionLocation = this.props.match.params.location;
      newPatient.ethnicity = this.props.match.params.ethnicity;
      newPatient.htn = this.props.match.params.htn === 'true'
      newPatient.hxSAH = this.props.match.params.sah === 'true'
            newPatient.studyPref= this.props.match.params.studyPref
      this.props.updatePatient(newPatient);
    }
  }
  constructor(props) {

    super(props);
      var graphProbLoc = 'isuia'
    switch (this.props.match.params.studyPref) {
      case '1':
        graphProbLoc = 'ucas'
        break;
      case '2':
        graphProbLoc = 'isuia'
        break;
      case '3':
        graphProbLoc = 'phases'
        break;
      case '4':
        graphProbLoc = 'isuia'
        break;
      default:

        break;
    }

    this.state = {
      isLarge: window.innerWidth > large_threshold,
      location: this.props.match.params.location !== 'undefined'
        ? locationNumberToName[this.props.match.params.location]
        : '',

      graphProb: graphProbLoc
    }

    this.updatePatientWithParams();

  }
  componentWillMount() {
    window.scrollTo(0, 0);
    window.addEventListener('resize', () => {
      this.setState({
        isLarge: window.innerWidth > large_threshold
      });
    }, false);

  }

  componentDidUpdate() {
    var hash = this.props.location.hash.replace('#', '');
    if (hash) {
      let node = ReactDOM.findDOMNode(this.refs[hash]);
      if (node) {
        node.scrollIntoView()
        window.scrollBy(0, -100);
      }
    }
  }
  urlWithParams(base) {
    var params = "/";

    if(this.props.match.params.sah !== undefined){
      params = params + this.props.patient.sex + '/' + this.props.patient.age + '/' + this.props.patient.lesionDiameter + '/' + this.props.patient.lesionLocation + '/' + this.props.patient.ethnicity + '/' + this.props.patient.htn + '/' + this.props.patient.hxSAH + '/' + this.props.patient.studyPref
    }
    var url = '#/' + base + params;
    return url;
  }


  pronounce() {
    pron.play();
  }

  locationStatement() {
    var div = null;
    if (typeof this.state.location !== 'undefined') {
      div = " Your aneurysm is located on the " + locationNumberToName[this.props.match.params.location] + ".";
    }
    return div;
  }



  setGraphProb(study) {
    this.setState({graphProb: study});
  }

  lifetimeRisk(annualRuptureRisk) {
    var remaining = lifeExpectancy[this.props.patient.sex][this.props.patient.age];
    var val = (((remaining) * (1 - (1 - annualRuptureRisk / 100))) * 100).toFixed(2)
    if(val > 100) {
    val =100}
    return val ;
  }

  compareChart() {
    var valMon = parseInt(this.lifetimeRisk(this.props.patient[this.state.graphProb] / 5));
    if (valMon > 100) {
      valMon = 100
    }
    var valClip = parseInt(this.props.patient.isuiaSurg);;
    var valCoil = parseInt(this.props.patient.isuiaEndo);

    var dataBad = [
      {
        Choice: 'No Surgery',
        Risk: valMon
      }, {
        Choice: 'Coiling',
        Risk: valCoil
      }, {
        Choice: 'Clipping',
        Risk: valClip
      }
    ];



    return (<VictoryChart domainPadding={20} theme={VictoryTheme.material}>
      <VictoryAxis tickValues={[1, 2, 3]} tickFormat={["No Surgery", "Coiling", "Clipping"]}/>
      <VictoryAxis domain={[0,100]} dependentAxis={true} axisLabelComponent={<VictoryLabel dy = {-30} />} label={'Risk (%)'} tickFormat={(x) => `${x}%`}/>
      <VictoryStack>
        <VictoryBar data={dataBad} x={'Choice'} y={'Risk'} style={{
            data: {
              fill: "#be202e"
            }
          }}/>

      </VictoryStack>
    </VictoryChart >);
  }

  render() {
    var size_class = this.state.isLarge
      ? 'large'
      : '';
    return (<div>
              <Header patient={this.props.patient}/>

        <Grid container={true} item={true} justify={'flex-start'} alignContent={'flex-start'} className={'first-block main-content'}xs={12}>

          <Paper>
            <Grid container={true} direction={'column'} alignContent={'stretch'}>
              <Grid item={true} container={true} xs={12}>
                <Typography variant='h3' className={'landing-title'}>About Options:</Typography>
              </Grid>
              <Grid id={'risk'} item={true} container={true} spacing={16} xs={12} className={'content-block'} alignItems={'center'} >

                {this.props.match.params.sah != null? <>
                <Grid item={true} xs={12} container={true} direction={size_class=='large' ? 'row':'column'}>
                  <Grid item={true} xs={12} container={true}>
                  <Grid container={true} item={true} xs={12} sm={6} direction={'row'} alignItems={'start'}>
                      <Grid item={true} container={true} direction={'column'} xs={12}>
                        <Typography variant={ 'h5'}>My Risk of Rupture in Next 5 Years:</Typography>
                        <Typography variant={'body1'}>These numbers come from major studies of patients with unruptured aneurysms.   {this.props.patient.studyPref == 4? <>Click on a number to see it shown in the graph to the right.</>:null}</Typography>

                    </Grid>
                      <Grid item={true} container={true} spacing={16} direction={'row'} xs={12} justify={ 'center'}>
                        {this.props.patient.ucas !== null & (this.props.patient.studyPref == 1 | this.props.patient.studyPref == 4) ?
                        <Grid item={true} container={true} direction={ 'column'} alignItems={ 'center'} xs={12} onClick={()=> this.setGraphProb('ucas')}>
                          <p className={ 'supertitle'}>UCAS Study</p>
                          <Typography variant={ 'h5'} justify={ 'center'} color={ 'primary'} className={ 'risk-number'}>5 Year Risk: {this.props.patient.ucas}%</Typography>
                          { this.props.patient.age
                          <=75 & this.props.patient.age> 50 ?
                            <Typography variant={ 'body1'} justify={ 'center'} color={ 'secondary'} className={ 'lifetime-mortality'}>Calculated Lifelong Risk: {this.lifetimeRisk(this.props.patient.ucas / 5)}%</Typography>
                            : null }
                        </Grid>
                        : null} {this.props.patient.studyPref == 2 | this.props.patient.studyPref == 4 ?
                        <Grid item={true} container={true} direction={ 'column'} alignItems={ 'center'} xs={12} onClick={()=> this.setGraphProb('isuia')}>
                          <p className={ 'supertitle'}>ISUIA Study</p>
                          <Typography variant={ 'h5'} justify={ 'center'} color={ 'primary'} className={ 'risk-number'}>5 Year Risk: {this.props.patient.isuia}%</Typography>
                          { this.props.patient.age
                          <=75 & this.props.patient.age> 50 ?
                            <Typography variant={ 'body1'} justify={ 'center'} color={ 'secondary'} className={ 'lifetime-mortality'}>Calculated Lifelong Risk: {this.lifetimeRisk(this.props.patient.isuia / 5)}%</Typography>
                            : null }
                        </Grid> : null}
                        {this.props.patient.studyPref == 3 | this.props.patient.studyPref == 4 ?
                        <Grid item={true} container={true} direction={ 'column'} alignItems={ 'center'} xs={12} onClick={()=> this.setGraphProb('phases')}>
                          <p className={ 'supertitle'}>PHASES Study</p>
                          <Typography variant={ 'h5'} justify={ 'center'} color={ 'primary'} className={ 'risk-number'}>5 Year Risk: {this.props.patient.phases}%</Typography>
                          { this.props.patient.age
                          <=75 & this.props.patient.age> 50 ?
                            <Typography variant={ 'body1'} justify={ 'center'} color={ 'secondary'} className={ 'lifetime-mortality'}>Calculated Lifelong Risk: {this.lifetimeRisk(this.props.patient.phases / 5)}%</Typography>
                            : null }
                        </Grid>
                        : null}
                      </Grid>
                  </Grid>
                  <Grid item={true} xs={12} sm={6}  container={true} direction={ 'column'} alignItems={ 'center'} justify={ 'center'} className={ 'norm_padding'}>
                    <Typography variant={ 'body1'}>
                      <b>Graph of Risk from <span className={'all-caps'}>{this.state.graphProb}</span> Study</b>
                    </Typography>
                    <ProbGraph prob={this.props.patient[this.state.graphProb]}/>
                  </Grid>
                  </Grid>
                  <Grid item={true} xs={12} container={true} justify={ 'center'} alignContent={ 'center'} style={{ 'paddingTop' : '30px'}}>
                    <Typography variant={ 'body1'} className={ 'callout'}>These numbers are an <b>estimate</b> for the average patient like you. The numbers come from 3 large studies of patients with aneurysms. Your medical provider will help you understand how your individual risk relates to these estimates.</Typography>
                  </Grid>
                </Grid></>:<div>
                  <Typography variant={ 'h6'} gutterBottom={true}>What is my risk of rupture?</Typography>
                  <Typography variant={ 'body1'} gutterBottom={true} paragraph={true}>There are 3 large studies of aneurysms that are used to predict how likely an aneurysm is to rupture. If you know details about your aneurysm, you can use the calculator below to see an estimate of your individual risk. Please note, that the risk
                    may be more or less as accurate for certain patients depending on how many patients like you were in the study. our actual risk of rupture may be higher or lower based on other parts of your health history or characteristics of your aneurysm.</Typography>
                  <Button fullWidth={true} variant={ 'outlined'} color={ 'primary'} href="#/initdata">Risk Calculator</Button>
                </div>
                }
              </Grid>
              <Divider/>
              <Grid id={'options'} item={true} container={true} className={'content-block'} direction={'column'}>
                <Typography variant={'h5'} gutterBottom={true}>
                  What are my treatment options?
                </Typography>
                <Typography variant={'body1'} className={'limit_lg'} gutterBottom={true} paragraph={true}>All patients should reduce their risk of rupture through lifestyle modification, and many will need to do nothing more than this. For patients with a higher risk of rupture in their lifetime, there are two general approaches to treatment: <b>watchful waiting and surgery.</b> The best option for you will depend on how likely it is that your aneurysm will rupture balanced against your individual needs and the risks of surgery.
                </Typography>
              </Grid>
              <Divider/>
              <Grid direction={size_class === 'large' ? 'row': 'column-reverse'} item={true} container={true} xs={12} className={'content-block'}>
                <Grid item={true} container={true} sm={8} direction={'column'} justify={'flex-start'} alignItems={'flex-start'} spacing={16}>
                <Grid item={true} className='landing_text_block'>
                  <Typography gutterBottom={true} variant="h6" component="h2">
                    For all patients: lifestyle changes
                  </Typography>
                  <Typography gutterBottom={true} variant="subtitle2" component="h2">
                    Goal: reduce risk of rupture and improve general health
                  </Typography>
                  <Typography variant={'body1'}>
                    There are some health conditions and behaviors that are known to increase the chance of rupture. These include smoking and high blood pressure. For certain low-risk aneurysms, this may be all that is required
                  </Typography>
                </Grid>
                <Button href={this.urlWithParams('about-rfm')} color={'primary'} size={'small'}>Learn More about Risk Reduction
                  <ChevronRight/></Button>
              </Grid>
              <Grid item={true} xs={12} sm={4} className={'norm_padding'}>
                <img src={donothing_img} alt={'Unavailable at this time.'} href={'#/about-rfm'} className={'limit_md grid_img'}/>
              </Grid>
            </Grid>

            <Divider/>
            <Grid item={true} container={true} direction={size_class === 'large'
                ? 'row'
                : 'column'} xs={12} spacing={16} className={'content-block  '}>
              <Grid item={true} xs={12} sm={6}>
                <Card >
                  <CardMedia>
                    <img src={angio_img} alt={'Unavailable at this time.'} className={'limit_md grid_img'}/>
                  </CardMedia>

                  <CardContent>

                    <Typography gutterBottom={true} variant="h6" component="h2" style={{
                        'paddingTop' : 16
                      }}>
                      For some patients: watchful waiting
                    </Typography>
                    <Typography gutterBottom={true} variant="subtitle2" component="h2">
                      Goal: monitor aneurysm for signs that rupture is more likely
                    </Typography>
                    <Typography component="p">
                      For some aneurysms with a low risk of rupture, it is best not to perform a risky procedure. However, it can be helpful to wait some time and then test again to see whether the anuerysm is growing or changing shape - a sign that the anuerysm is more likely to rupture.
                    </Typography>
                  </CardContent>

                  <CardActions>
                    <Button href={this.urlWithParams('about-img')} color={'primary'} size={'small'}>Learn More about Watch and Wait
                      <ChevronRight/></Button>

                  </CardActions>
                </Card>
              </Grid>
              <Grid item={true} xs={12} sm={6}>
                <Card >

                  <CardMedia>
                    <img src={coiling_img} alt={'Unavailable at this time.'} className={'limit_md grid_img'}/></CardMedia>

                  <CardContent>

                    <Typography gutterBottom={true} variant="h6" component="h2" style={{
                        'paddingTop' : 16
                      }}>
                      For some patients: surgery
                    </Typography>
                    <Typography gutterBottom={true} variant="subtitle2" component="h2">
                      Goal: treat aneurysm to eliminate risk of rupture
                    </Typography>
                    <Typography component="p">
                      For anuerysms that are more likely to rupture, it may be worth the risk to do surgery to treat the aneurysm. The intent of these surgeries is to prevent aneurysms from ruptureing in the future by preventing blood from flowing into them. There are two main kinds of surgery, <b>clipping</b> and endovascular <b>coiling</b>, each of which has different risks.
                    </Typography>
                  </CardContent>

                  <CardActions>
                    <Button href={this.urlWithParams('about-surg')} color={'primary'} size={'small'}>Learn More about Surgery
                      <ChevronRight/></Button>

                  </CardActions>
                </Card>
              </Grid>

            </Grid>

            <Divider/>
            <Grid id={'decisionframe'} item={true} container={true} spacing={16} xs={12} className={'content-block'} alignItems={'center'}>
              <Grid item={true} xs={12} sm={6} className='landing_text_block'>

                <Typography variant={'h5'} gutterBottom={true}>Do I need surgery on my aneurysm?</Typography>
                <Typography variant={'body1'} paragraph={true}>Studies have been done that help doctors predict which aneurysms may rupture. Although it might seem like a good idea to treat all aneurysms just in case, the treatments themselves can cause damage - a procedural "complication." For this reason, the risk and benefits of treatment must be balanced, and this balance will be different for every person and shoudl be dicussed with your doctor.
                </Typography>
          {this.props.match.params.size <= 7 ? <Typography variant={'body1'} paragraph={false}>Even if there is a relatively high lifetime risk of rupture, for some patients it may be better to observe the aneurysm for some time to see whether it shows sign of growth and potential rupture before treating. Many will never show these signs, and surgery can be avoided.
                </Typography>: null}
                {this.props.match.params.sah != null ? <Typography variant={'body1'} paragraph={false}>The graph on the right shows the risk of a bad neurological outcome with three different choices. "No Surgery" includes both watchful waiting and doing nothing at all. "Coiling" gives the risk associated with coiling by endovascular coiling. "Clipping" gives the risk associated with clipping through open surgery.</Typography> :null }
              </Grid>
              {
                this.props.match.params.sah != null
                  ? <Grid item={true} xs={12} sm={6} container={true} justify={'center'} alignItems={'center'} direction={'column'}>
                      <Typography variant={'body1'}>
                        <b>Risk of Bad Outcome with Different Management Options</b>
                      </Typography>


                      {this.compareChart()}

                    </Grid>

                  : null
              }
              {
                this.props.match.params.sah !== null
                  ? <Typography className={'callout'}>Monitor data is the calculated lifetime risk of rupture drawn from
                      <span className={'all-caps'}>{this.state.graphProb}</span>
                      data and US census data on life expectancy. Endovascular and surgery outcomes are drawn from ISUIA. These numbers are <b>estimates</b>, and you should discuss with your medical provider to better understand your individual risk.
                    </Typography>
                  : null
              }

              <Grid item={true} container={true} xs={12} direction={'row'} justify={'space-between'} className={'norm_padding'} >


                <Button variant={'contained'} color={'primary'} href={this.urlWithParams('home')} ><ChevronLeft/>Back
                  </Button>
                <Button href='#/options4' color={'primary'} size={'large'} variant={'contained'}>Compare Options<ChevronRight/></Button>
              </Grid>
            </Grid>
          </Grid>
        </Paper>
      </Grid>


  </div >)
  }
}
export default Decision;
