import React from 'react';

import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper';

import PatientDataForm from '../components/patientDataForm.jsx'

import {locationNumberToName} from '../files/constants.js'
import Header from '../components/Header.jsx'
import '../App.css';

class InitData extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sex: this.props.patient.sex,
      age: this.props.patient.age,
      lesionLocation: this.props.patient.lesionLocation,
      lesionDiameter: this.props.patient.lesionDiameter,
      htn: this.props.patient.htn,
      ethnicity: this.props.patient.ethnicity,
      hxSAH: this.props.patient.hxSAH
    }
  }

  handleTextChange = name => event => {
    this.setState({[name]: event.target.value});

  };
  handleToggleChange = name => event => {
    this.setState({[name]: event.target.checked});

  };

  locationOptions() {
    var div = Object.keys(locationNumberToName).map((key) => {
      return (<option key={key} value={key}>{locationNumberToName[key]}</option>)
    });
    return div;
  };

  render() {
    return (<div>
              <Header patient={this.props.patient}/>
      <Grid container={true} justify={'flex-start'} alignContent={'flex-start'} spacing={8} className={'norm_padding main-content'}>

        <Grid item={true} xs={12}>
          <Paper className={'norm_padding'}>
            <PatientDataForm updatePatient={(pat) => this.props.updatePatient(pat)} patient={this.props.patient} destination={'home'} skip={true}/>
          </Paper>
        </Grid>

      </Grid>
    </div>);
  }
}

export default InitData;
