import React from 'react'
import Typography from '@material-ui/core/Typography'


import Grid from '@material-ui/core/Grid'


export default class Footer extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      menu: false,
      anchorEl: null,
      setAnchorel: null

    }
  }


  openMenu(event) {
    this.setState({
      menu: !this.state.menu,
      anchorEl: event.currentTarget
    })
  }

  closeMenu() {
    this.setState({
      menu: !this.state.menu,
      anchorEl: null,
    })
  }


  render() {
    return (<Grid item={true} container={true} direction ={'row'} xs={12} className={'content-block footer'}>
    <Typography variant={'body1'} className={'disclaimer'}>Designed by Josh Feler. Special thanks to Dr. Charles Matouk and Dr. David Hwang for guiding the development of this tool. This tool represents the opinion of the designer and is meant to provide general education - it is not a conclusive resource. Please consult with your treating provider to understand your individual risks and treatment options.</Typography>
  </Grid>
)
}}
