import React from 'react'
import Typography from '@material-ui/core/Typography'
import '../App.css';


class ProbGraph extends React.Component {
  constructor(props) {
    super(props);

  }

  render() {
    var prob = Math.round(this.props.prob)
    var src = "http://odds-view.appspot.com/api/draw?odds=" + prob + ":100&shape=person_rect&color1=BBBBBB&color2=dd5542"
    return (<><img src={src} alt={'Unavailable at this time.'} className={'grid_img'}/>
    <Typography variant={'body1'} > {this.props.prob} of 100 people will rupture.</Typography>
    <Typography variant={'body1'}>{100 - this.props.prob} of 100 people will not rupture.</Typography></>)
  }
}

export default ProbGraph;
